import {
  Input,
  Output,
  EventEmitter,
  ViewChild,
  ElementRef,
} from "@angular/core";
import { SHCtlCommon } from '../components/sh-ctl-common';
import { MaxByteLengthHistory } from "./MaxByteLengthHistory";

export class SHFormControl<T> {
  //è¦ç´ ã³ã³ãã­ã¼ã«å
  @Input() public ctlName: string = "SHFormControl";
  //è¡¨ç¤ºã¬ã¤ã¢ã¦ãåãæ¿ããã©ã°(true:é²è¦§å°ç¨/false:ç·¨éå¯è½)
  @Input() public isDisabled: boolean = false;
  //å¤ãã¤ã³ãå¤æ°
  @Input() public value: T = null;
  //ã¿ã¤ãã«å
  @Input() public title = " ";
  //ã³ã³ãã­ã¼ã«æå(auto/always/nevwer)
  @Input() public floatLabel = "auto";
  //é²è¦§å°ç¨ç»é¢ã§ã®ã©ãã«å¹(px)
  @Input() public labelWidth = "150";
  //inputè¦ç´ ã«å¯¾ããæå¤§å¥åæå­æ°(è¨­å®å¯è½ãªã³ã³ãã­ã¼ã«ã®ã¿é©ç¨)
  @Input() public maxlength: number;
  //inputè¦ç´ ã«å¯¾ããæå¤§å¥åbyteæ°(è¨­å®å¯è½ãªã³ã³ãã­ã¼ã«ã®ã¿é©ç¨)
  @Input() public maxByteLength: number;
  //inputè¦ç´ ã«å¯¾ããstyleã®æå®(å½¢å¼ï¼{ color: "red" })
  @Input() public styles: any = {};
  //inputè¦ç´ ã«å¯¾ããclassã®æå®(å½¢å¼ï¼{ "className": true } or "className className2")
  @Input() public classes: any = {};
  //inputè¦ç´ ã«å¯¾ããå¿é å¥åãã©ã°
  @Input() public required: boolean = false;
  //inputè¦ç´ ã«å¯¾ããç¶æã®æå®
  @Input() public state: string = "";
  //inputè¦ç´ ã«å¯¾ããæè¨ã®æå®
  @Input() public message: string[] = [""];
  //radio,selectã«å¯¾ããééä¿¡ç¨ãã¼ã¿ã®åãæ¸¡ã
  @Input() public items: any[];
  //inputè¦ç´ ã«å¯¾ããå¿é å¥åãã©ã°
  @Input() public disabled: boolean = false;

  //å¤ãã¤ã³ãçµæã®è¿å´ãæã
  @Output() public valueChange = new EventEmitter();

  //BeforeClickã¤ãã³ãè¿å´
  @Output() public beforeClick = new EventEmitter();
  //AfterClickã¤ãã³ãè¿å´
  @Output() public afterClick = new EventEmitter();
  //BeforeFocusã¤ãã³ãè¿å´
  @Output() public beforeFocus = new EventEmitter();
  //AfterFocusã¤ãã³ãè¿å´
  @Output() public afterFocus = new EventEmitter();
  //BeforeBlurã¤ãã³ãè¿å´
  @Output() public beforeBlur = new EventEmitter();
  //AfterBlurã¤ãã³ãè¿å´
  @Output() public afterBlur = new EventEmitter();
  //BeforeChangeã¤ãã³ãè¿å´
  @Output() public beforeChange = new EventEmitter();
  //AfterChangeã¤ãã³ãè¿å´
  @Output() public afterChange = new EventEmitter();
  @ViewChild(`inputElm`, { static: false }) selfInputElm: ElementRef<HTMLInputElement>;

  public objClass: object = {};
  public strClass: string = "";
  public isComp = false;
  public array: any[] = [];
  public maxByteLengthHistory: MaxByteLengthHistory;
  
  //forcusã®ä¿æå¤æ°
  public isfocused: boolean = false;

  @Input() public onlyField: boolean = false;

  constructor() {}

  public onInit() {
    //ã¯ã©ã¹ã®æå®æ¹æ³ã«ãã£ã¦åå²ãã
    if (typeof this.classes === "string") {
      this.strClass = this.classes;
      this.objClass = null;
    } else {
      this.strClass = "";
      this.objClass = this.classes;
    }
  }

  /**
   * onInput
   * @param event input
   */
  public onInput(event) {}

  /**
   * onKeyDown
   * @param event keydown
   */
  public onKeyDown(event) {}

  
  ngAfterViewInit() {
    if (!this.maxByteLength) return;
    if (this.selfInputElm) {
      this.selfInputElm.nativeElement.addEventListener(
        "keydown",
        this.handleKeydown.bind(this)
      );
      this.selfInputElm.nativeElement.addEventListener(
        "input",
        this.handleInput.bind(this)
      );
      this.selfInputElm.nativeElement.addEventListener(
        "compositionstart",
        this.handleCompositionStart.bind(this)
      );
      this.selfInputElm.nativeElement.addEventListener(
        "compositionend",
        this.handleCompositionEnd.bind(this)
      );
    }
  }

  /**
   *maxByteLengthã®å¶å¾¡
   */
  protected handleKeydown(e) {
    if (!this.maxByteLength) return;
    const value = e.target.value;
    if (
      value &&
      !this.maxByteLengthHistory &&
      !MaxByteLengthHistory.isAllowOperation(e)
    ) {
      this.maxByteLengthHistory = new MaxByteLengthHistory(
        value,
        this.selfInputElm.nativeElement.selectionStart,
        this.maxByteLength
      );
    }
  }
  /**
   *@ ãã¨ä½byteè¨±å®¹ã§ããããæ´æ°
   */
  protected handleInput(e) {
    if (!this.maxByteLength) return;
    this.maxlength = undefined;
    if(!this.maxByteLengthHistory && e.target.value) {
      this.maxByteLengthHistory = this.maxByteLengthHistory = new MaxByteLengthHistory(
        e.target.value,
        this.selfInputElm.nativeElement.selectionStart,
        this.maxByteLength
      );
    }
    if (this.maxByteLengthHistory) {
      this.maxByteLengthHistory.changeEnterableBytes(e.target.value);
      if (!this.maxByteLengthHistory.getEnterableBytes() && !this.isComp) {
        const maxLen = this.maxByteLengthHistory.getMaxLength(e.target.value);
        this.maxlength = maxLen;
        if(e.target.value.length > maxLen) {
          e.target.value = e.target.value.substr(0, maxLen)
          this.valueChange.emit(e.target.value)
        }
        
      }
    }
  }

  protected handleCompositionStart(e) {
    if (!this.maxByteLength) return;
    this.isComp = true;
    this.maxlength = undefined;
    if(e.target.value) {
      this.maxByteLengthHistory = this.maxByteLengthHistory = new MaxByteLengthHistory(
        e.target.value,
        this.selfInputElm.nativeElement.selectionStart,
        this.maxByteLength
      );
    }else {
      this.isComp = false;
    }
  }
  protected handleCompositionEnd(e) {
    if (!this.maxByteLength) return;
    const result = this.maxByteLengthHistory.getResult(
      e.data,
      e.target.value.replace(/\n/g, '##')
    ).replace(/##/g, '\n');
    e.target.value = result;
    this.valueChange.emit(result);
    this.maxByteLengthHistory = null;
    this.isComp = false;
  }
  /**
   * onFocusin
   * @param event focusin
   */
  public onFocusin(event) {
    this.isfocused = true;
  }

  /**
   * onFocusout
   * @param event focusout
   */
  public onFocusout(event) {
    this.isfocused = false;
    setTimeout(() => {
      if (!this.isfocused) this.onBlur(event);
    }, 0);
  }

  /**
   * clickClearButton
   * @param event keydown
   */
  public clickClearButton(event) {
    if (event.keyCode == SHCtlCommon.Const.keyCodes.enter ||
      event.keyCode == SHCtlCommon.Const.keyCodes.space )
    {
      //ã¨ã³ã¿ã¼ã­ã¼ã§å¥ã³ã³ãã­ã¼ã«ã«ãã©ã¼ã«ã¹ãç§»åããªãããã«ãã©ã¼ã«ã¹ãå½ã¦ç´ã
      setTimeout(() => {
        let inputValue = <HTMLInputElement>document.querySelector(`textarea[name=` + this.ctlName + `], input[name=` + this.ctlName + `]`);
        inputValue.dispatchEvent(new Event('focus'));          
      }, 0);
      this.clearValue();
      return false;
    }
  }
  
  /**
   * onChange
   * @param event change
   */
  public onChange(event) {
    this.valueChange.emit(this.value);
  }

  /**
   * onFocus
   * @param event focus
   */
  public onFocus(event) {
    event.target.select();
  }

  /**
   * onBlur
   * @param event blur
   */
  public onBlur(event) {}

  /**
   * onBeforeClick
   * @param event
   */
  public onBeforeClick(event) {
    this.beforeClick.emit(event);
  }

  /**
   * onAfterClick
   * @param event
   */
  public onAfterClick(event) {
    this.afterClick.emit(event);
  }

  /**
   * onBeforeFocus
   * @param event
   */
  public onBeforeFocus(event) {
    this.beforeFocus.emit(event);
  }

  /**
   * onAfterFocus
   * @param event
   */
  public onAfterFocus(event) {
    this.afterFocus.emit(event);
  }

  /**
   * onBeforeBlur
   * @param event
   */
  public onBeforeBlur(event) {
    this.beforeBlur.emit(event);
  }

  /**
   * onAfterBlur
   * @param event
   */
  public onAfterBlur(event) {
    this.afterBlur.emit(event);
  }

  /**
   * onBeforeChange
   * @param event
   */
  public onBeforeChange(event) {
    this.beforeChange.emit(event);
  }

  /**
   * onAfterChange
   * @param event
   */
  public onAfterChange(event) {
    this.afterChange.emit(event);
  }

  /**
   * å¤ã®ã¯ãªã¢
   */
  public clearValue() {
    this.value = null;
    let inputValue = <HTMLInputElement>(
      document.querySelector(
        `textarea[name=` + this.ctlName + `], input[name=` + this.ctlName + `]`
      )
    );
    //ã¿ã¤ãã³ã°ãéãããªãã¨changeã¤ãã³ãåã®event.target.valueãåã®å¤ã«ãªã
    setTimeout(function(){
      inputValue.dispatchEvent(new Event('change'));
      inputValue.dispatchEvent(new Event('focus'));
    },10);   

  }
}
