import { Inject, Component, OnInit, AfterViewChecked } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { SHCtlCommon } from '../sh-ctl-common';

interface Data {
    dataTitle: string
    hiddenColumns: any
}
@Component({
    selector: 'sh-ctl-popup-dialog',
    templateUrl: 'sh-ctl-popup-dialog.component.html',
    styleUrls: ['sh-ctl-popup-dialog.component.css', '../../../style.common.css']
})
export class SHCtlPopupDialogComponent implements OnInit, AfterViewChecked {
    public displayedColumns: string[] = [];
    public name: string = "";
    public uniqueStr = "";
    public dataTitle: string;
    public codes: any;
    public unchangeableCodes: any;
    public codesHeader: any;

    constructor(
        private dialogRef: MatDialogRef<SHCtlPopupDialogComponent>,
        @Inject(MAT_DIALOG_DATA) private data: Data,
    ) { }

    /**
     * å¤ããã¤ã³ããããæã«å¼ã°ãã
     */
    ngOnInit() {
        this.init()
    }

    /**
    * å¤ã®åæå
    */
    private init() {
        this.dataTitle = this.data.dataTitle;
        let cols = [];
        this.getHashKeys().forEach((name, idx) => {
            if (!this.data.hiddenColumns.find(_ => _ == (idx + 1))) cols.push(name);
        });
        this.displayedColumns = cols;
        this.uniqueStr = SHCtlCommon.getUniqueStr();
    }

    /**
    * ãã¼ãã«ã®ãããã¼ãç·¨é
    */
    ngAfterViewChecked() {
        let headerElement = document.getElementsByTagName('th')
        let countBlank = 0;
        if (!this.codesHeader) return;
        for (let i = 0; i < headerElement.length; i++) {
            if (this.codesHeader[i] != "") {
                for (let e = 1; e <= countBlank; e++) {
                    headerElement[i - e].style.display = 'none';
                }
                countBlank = 0;
            } else if (this.codesHeader[i] === "") {
                countBlank += 1;
                headerElement[i - countBlank].colSpan = countBlank + 1;
                headerElement[i - countBlank].style.textAlign = 'center';
                if (i === headerElement.length - 1) {
                    for (let j = 0; j < countBlank; j++) {
                        headerElement[i - j].style.display = 'none';
                    }
                }
            }
        }

    }

    /**
     * ãã¤ã¢ã­ã°ãéãã
     */
    public closeDialog() {
        this.dialogRef.close()

    }

    /**
     * ãããã¢ããããé¸æããããã¼ã¿ãè¦ªã«è¿ã
     * @param item äºæ¥­æã³ã¼ãä¸è¦§ããé¸æããã1ã¤
     */
    public selectCode(item: any) {
        //ãã¤ã¢ã­ã°ãéããæã«å¤ãè¦ªã«æ¸¡ã
        this.dialogRef.close(item)

    }

    /**
     * ãã¼ã¿ã®keyãè¿ã
     */
    public getHashKeys() {
        if (!this.unchangeableCodes || this.unchangeableCodes.length == 0) {
            return []; 
        } else {
            return Object.keys(this.unchangeableCodes[0]);
        }
    }

    /**
     * ãããã¢ããåã§ã®ææ§æ¤ç´¢
     * ã¹ãã¼ã¹åºåãã§1ã­ã¼ã¯ã¼ãã¨ãã
     * @param event inputã¤ãã³ã
     */
    public filter(event) {
        if (!this.unchangeableCodes) return;
        const filterString: string = event.target.value;
        let fileterStrArray = filterString.replace(/ã/g, " ").split(" ");
        fileterStrArray = fileterStrArray.filter((value) => {
            return value;
        });
        let resultArray: any[];
        resultArray = this.unchangeableCodes.concat();
        for (let i = 0; i < fileterStrArray.length; i++) {
            let obj = this;
            resultArray = resultArray.filter(function (value: any) {
                let values = Object.values(value)
                for (let j = 0; j < values.length; j++) {
                    if (obj.data.hiddenColumns.find(_ => _ == (j + 1))) continue;
                    if (values[j] != null && values[j].toString().match(fileterStrArray[i])) {
                        return true;
                    }
                }
                return false;
            });
        }
        this.codes = resultArray;
    }
}