import { Pipe, PipeTransform } from '@angular/core';

//Dateåã®ãã¼ã¿ãsh-ctl-dateã§æ±ãå½¢å¼ã®ãªãã¸ã§ã¯ãã«å¤æ
@Pipe({
  name: 'ngDateValue'
})
export class NgDateValuePipe implements PipeTransform {
  transform(value: Date): any {
    if (value != null) {
      return {
        year: value.getFullYear(),
        month: value.getMonth() + 1,
        day: value.getDate()
      };
    }
    return null;
    
  }
}
