import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class LockScreenService {
  private isConnecting: boolean = false;
  private isConnectingSubject = new Subject<boolean>();
  public isConnectingState = this.isConnectingSubject.asObservable();
  public count :number = 0;

  /**
  * ã³ã³ã¹ãã©ã¯ã¿
  */
  constructor() { }

  /**
  * ç»é¢ãã­ãã¯ãã(ã¦ã¼ã¶ã¼ãæä½åºæ¥ãªãããã«ãã)
  */
  public lockScreen() {
      this.count += 1;
      if (this.count === 1) {
        this.isConnecting = true;
        this.isConnectingSubject.next(this.isConnecting);
      }
  }

  /**
  * ç»é¢ã®ã­ãã¯ãè§£é¤ãã(ã¦ã¼ã¶ã¼ãæä½åºæ¥ãç¶æã«ãã)
  */
  public unlock() {
    this.count -= 1;
    if (this.count === 0) {
        this.isConnecting = false;
        this.isConnectingSubject.next(this.isConnecting);
    }
  }
}
